@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap);
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
      font-family: 'Roboto Slab', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/** @format */
:root,
* {
	box-sizing: border-box;
	/* padding: 0;
	margin: 0; */
}
.app-container {
	box-sizing: border-box;
	background-color: #444;
	height: 100vh;
	overflow-y: hidden;
}

/** @format */

.nav-container {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.title {
	color: goldenrod;
}
.nav-elements {
	flex-basis: 320px;
	padding-left: 0;
	display: flex;
	justify-content: space-between;
	list-style: none;
	text-decoration: none;
}
.nav-elements li {
	color: #ddd;
}
.link {
	text-decoration: none;
}

/** @format */
.home-container {
	margin-top: 7rem;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}
.dp {
	width: 250px;
	border-radius: 50%;
	border: 3px solid #ddd;
	padding: 0.5rem;
	box-shadow: 2px 2px 3px #ddd;
}
.dp:hover {
	border-color: goldenrod;
	box-shadow: 2px 2px 3px;
}
.home-info {
	align-self: flex-start;
	color: #ddd;
	font-family: 'Courier New', Courier, monospace;
	text-shadow: 2px 2px 2px #222;
}
.home-info-title {
	font-size: 4rem;
	letter-spacing: 1px;
	margin-bottom: 1rem;
	margin-top: 1rem;
}
.bold {
	color: goldenrod;
	letter-spacing: 1px;
}
.cv-btn {
	cursor: pointer;
	color: goldenrod;
	background: none;
	padding: 0.5rem 1rem;
	font-size: 1rem;
	letter-spacing: 1px;
	font-weight: 700;
	font-family: monospace;
}
.cv-btn:hover {
	color: #ddd;
	border-color: goldenrod;
}

/** @format */

.experience-container {
	width: 60%;
	margin: auto;
	margin-top: 5rem;
	color: #ddd;
}
.exp-title {
	width: 100%;
}
.exp-sub-container {
	display: flex;
}
.techs {
	padding: 0.5rem;
	background-color: goldenrod;
	flex-basis: 500px;
	border-radius: 5px;
	box-shadow: 2px 2px 2px 2px #444;
	margin-right: 1rem;
}
.techs h2 {
	color: #444;
	padding-left: 1rem;
}
.icon-container {
	width: 350px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding-bottom: 2rem;
}
.icon {
	font-size: 2.5em;
	color: #333;
	margin: 0.5rem;
}
.experience-info {
	position: relative;
	padding: 0 0 0 1rem;
	line-height: 1.5;
	font-family: monospace;
	font-size: 1rem;
}
.experience-info::before {
	content: '';
	border-left: 2px solid brown;
	position: absolute;
	left: 0;
	top: 10px;
	bottom: 10px;
	box-shadow: 2px 2px 2px #333;
}

